import { FC, forwardRef } from 'react';

import {
  Wrapper,
  Caption,
  FlushCaption,
  PaddedCaption,
  CaptionText,
} from './MediaPlusCaption.styles';
import {
  MediaPlusCaptionProps,
  MediaPlusCaptionForSliderProps,
} from './MediaPlusCaption.types';

export const MediaPlusFlushCaption: FC<MediaPlusCaptionProps> = ({
  className,
  caption,
  children,
}) => {
  return (
    <Wrapper className={className}>
      {children}
      <FlushCaption>{caption}</FlushCaption>
    </Wrapper>
  );
};

export const MediaPlusPaddedCaption: FC<MediaPlusCaptionProps> = ({
  className,
  caption,
  children,
}) => {
  return (
    <Wrapper className={className}>
      {children}
      <PaddedCaption>{caption}</PaddedCaption>
    </Wrapper>
  );
};

export const MediaPlusCaptionForSlider = forwardRef<
  HTMLDivElement,
  MediaPlusCaptionForSliderProps
>(function MediaPlusCaptionForSlider({ className, caption, children }, ref) {
  return (
    <Wrapper className={className}>
      {children}
      <Caption ref={ref}>
        <CaptionText>{caption}</CaptionText>
      </Caption>
    </Wrapper>
  );
});
